// *** =================================
// Tab devices  ***

@media only screen and (min-width: 768px) and (max-width: 1023px) {

    // *** Feature news section ***
    .feature-news-section{
        &__content{
            &--signle-news{
                .feature-image{
                    a{
                        img{
                            width: 140%;
                            margin-left : -20%;
                        }
                    }
                }
            }
            &--double-column {
                .double-item {
                    width: 100%;
                    &__title{
                        padding: 10px 0 0;
                        font-size : 14px;
                    }
                    &__image{
                        width: 25%;
                    }
                    &__description{
                        width: 72%;
                    }
                    
                }
            }
        }
    }


}

// *** =================================
// Mobile devices  ***

@media only screen and (max-width: 767px) {

    // *** Common css ***
    .custom-row{
        padding : 10px;
    }

    .desktop-hide{
        display : block;
    }

    .mobile-hide{
        display : none;
    }

    .flex-container{
        flex-wrap: wrap;
    }

    
    // *** Feature news section ***
    .feature-news-section{
        &__content{
            &--double-column {
                padding: 0 8px;
                .double-item {
                    width: 100%;
                    .flex-container{
                        justify-content : center;
                    }
                    &__title{
                        text-align: center;
                    }
                    &__image{
                        width: 100%;
                    }
                    &__description{
                        padding : 10px 0;
                    }
                }
            }
        }
    }


}